<template>
	<v-container>
		<TeamCreationBtn />
		<h1> </h1>
		<MyTeamComp
			v-for="(team, index) in teamList"
			:key="index"
			:pTeamInfo="team"
		/>

		<v-pagination
			v-model="pagination.pageNo"
			:length="pagination.totalPageCount"
			@input="searchMyTeams"
		/>
	</v-container>
</template>

<script>
	// Vue lifeCycle  
	// https://wormwlrm.github.io/2018/12/29/Understanding-Vue-Lifecycle-hooks.html
	import MyTeamAPI from '@/api/MyTeamAPI';
	import MyTeamComp from '@/views/myTeam/list/components/MyTeamComp.vue';

	import TeamCreationBtn from '@/views/myTeam/list/button/TeamCreationBtn.vue';

	import PaginationUtil from '@/common/util/PaginationUtil.js';

	export default {
		components: {
			MyTeamComp,
			TeamCreationBtn,
		},
		data: () => {
			return {
				teamList: [],
				pagination: PaginationUtil.getIntlPager(),
			};
		},
		methods: {
			async getMyTeams(params) {
				//  console.log    
				// https://kkangdda.tistory.com/81
				const res = await MyTeamAPI.searchTeams(params);
				this.teamList = res.data.myTeamDTOList;
				this.pagination = res.data.pagerDTO;
			},
			searchMyTeams() {
				const params = {
					pageNo: this.pagination.pageNo,
				};
				this.getMyTeams(params);
			},
		},
		mounted() {
			this.getMyTeams();
		},
	};
</script>

