<!--
#    :    . (API ) 
# Props 
1. pBtnInfo : {
	name : String ( ) /  : ""
	...  
}

2. pRouteInfo : {
	name : Stirng (  ) 
	params : Object (  )
}
-->

<template>
	<!-- TODO    -->
	<v-container>
		<v-row no-gutters justify="end">
			<v-col cols="1">
				<v-btn text color="primary" @click="this.moveRoutePage">
					{{ btnName }}
				</v-btn>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
	import ValidationUtil from '@/common/util/ValidationUtil.js';
	export default {
		props: {
			pBtnInfo: Object,
			pRouteInfo: Object,
		},
		data() {
			return {
				btnName: this.getBtnName(),
			};
		},
		methods: {
			getBtnName() {
				if (ValidationUtil.isNull(this.pBtnInfo.name)) {
					return '';
				}
				return this.pBtnInfo.name;
			},
			moveRoutePage() {
				const hasNotRouteInfo = ValidationUtil.isNull(this.pRouteInfo);
				if (hasNotRouteInfo) {
					return;
				}

				this.$router.push({
					name: this.pRouteInfo.name,
					params: this.pRouteInfo.params,
				});
			},
		},
	};
</script>

<style></style>
